<!-- Lynn -->
<script>
export default {
  name: "scheduleMainEdit-component",
  data() {
    return {
      form: {},
      rules: {
        room_No: [{ required: true, message: "必填", trigger: "change" }],
        begin_Time: [{ required: true, message: "必填", trigger: "change" }],
        end_Time: [{ required: true, message: "必填", trigger: "change" }],
      },
      maxStudent: [15, 20, 30, 35, 40, 50, 60, 70, 80, 100, 120, 140], // 最大開課人數
    };
  },
  computed: {
    // 教室地點選單
    allClassroom() {
      return this.$store.state.all_classroom;
    },
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    submitForm(formName) {
      if (this.form.begin_Time >= this.form.end_Time) {
        this.$message({
          type: "error",
          showClose: true,
          message: "上課時間有誤，請調整",
        });
      } else {
        this.$refs[formName].validate(async (valid) => {
          if (valid) {
            try {
              const res = await this.$store.dispatch(
                "updScheduleMain",
                this.form
              );
              if (res.data.isOK) {
                this.$message({
                  type: "success",
                  duration: 5000,
                  showClose: true,
                  message: "修改成功",
                });

                // 回上一頁 (教室/課程/課表管理)
                this.$router.push({ name: "courseInfoManagement" });
              }
            } catch (err) {
              console.log(err);
            }
          } else {
            this.$message({
              type: "error",
              showClose: true,
              message: "資料填寫錯誤",
            });
          }
        });
      }
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 將攜帶的資料，覆蓋於表單上
    let data = JSON.parse(this.$route.query.data);
    this.form = data;
    // 打 API 取得選單資料
    try {
      await this.$store.dispatch("forSchedule");
    } catch (err) {
      console.log(err);
    }
  },
};
</script>

<template src="./scheduleMainEdit.html"></template>
